	module.exports = {"data":{"markdownRemark":{"html":"<h3>Program Notifications</h3>\n<p>As a hacker, you’ll receive notifications (via the product and email) when programs you’re subscribed to make updates. Notifications in the product will appear in the notifications box next to your profile icon.</p>\n<blockquote>\n<p><strong>Note:</strong> Receiving program notifications to all program updates is currently only enabled for select hackers.</p>\n</blockquote>\n<p>\n  <a\n    class=\"gatsby-resp-image-link\"\n    href=\"/static/program-notifications-1-fa84767ad580ad6b5a61b842ed57d317-acc99.png\"\n    style=\"display: block\"\n    target=\"_blank\"\n    rel=\"noopener\"\n  >\n  \n  <span\n    class=\"gatsby-resp-image-wrapper\"\n    style=\"position: relative; display: block; ; max-width: 500px; margin-left: auto; margin-right: auto;\"\n  >\n    <span\n      class=\"gatsby-resp-image-background-image\"\n      style=\"padding-bottom: 123.36956521739131%; position: relative; bottom: 0; left: 0; background-image: url('data:image/png;base64,'); background-size: cover; display: block;\"\n    >\n      <img\n        class=\"gatsby-resp-image-image\"\n        style=\"width: 100%; height: 100%; margin: 0; vertical-align: middle; position: absolute; top: 0; left: 0; box-shadow: inset 0px 0px 0px 400px white;\"\n        alt=\"INSERT IMAGE\"\n        title=\"\"\n        src=\"/static/program-notifications-1-fa84767ad580ad6b5a61b842ed57d317-2f183.png\"\n        srcset=\"/static/program-notifications-1-fa84767ad580ad6b5a61b842ed57d317-67829.png 125w,\n/static/program-notifications-1-fa84767ad580ad6b5a61b842ed57d317-5c59f.png 250w,\n/static/program-notifications-1-fa84767ad580ad6b5a61b842ed57d317-2f183.png 500w,\n/static/program-notifications-1-fa84767ad580ad6b5a61b842ed57d317-acc99.png 736w\"\n        sizes=\"(max-width: 500px) 100vw, 500px\"\n      />\n    </span>\n  </span>\n  \n  </a>\n    </p>\n<p>Updates to these program settings will trigger notifications:</p>\n<ul>\n<li>Policy</li>\n<li>Bounty table</li>\n<li>Scope</li>\n<li>Hacker messages</li>\n</ul>\n<p>You’ll be subscribed to receive program update notifications if you’ve expressed interest in a program. This can happen in one of two ways:</p>\n<table>\n<thead>\n<tr>\n<th>Option</th>\n<th>Details</th>\n</tr>\n</thead>\n<tbody>\n<tr>\n<td>Manually</td>\n<td>You subscribed to receive notifications via a program’s Security Page by clicking \n<strong>Subscribe</strong>\n.\n<br>\n<br>\n\n  <a\n    class=\"gatsby-resp-image-link\"\n    href=\"/static/program-notifications-2-30633c0ff5509dcca0809a9eb683d079-1007a.png\"\n    style=\"display: block\"\n    target=\"_blank\"\n    rel=\"noopener\"\n  >\n  \n  <span\n    class=\"gatsby-resp-image-wrapper\"\n    style=\"position: relative; display: block; ; max-width: 500px; margin-left: auto; margin-right: auto;\"\n  >\n    <span\n      class=\"gatsby-resp-image-background-image\"\n      style=\"padding-bottom: 65.96638655462185%; position: relative; bottom: 0; left: 0; background-image: url('data:image/png;base64,'); background-size: cover; display: block;\"\n    >\n      <img\n        class=\"gatsby-resp-image-image\"\n        style=\"width: 100%; height: 100%; margin: 0; vertical-align: middle; position: absolute; top: 0; left: 0; box-shadow: inset 0px 0px 0px 400px white;\"\n        alt=\"subscribe button on policy page\"\n        title=\"\"\n        src=\"/static/program-notifications-2-30633c0ff5509dcca0809a9eb683d079-2f183.png\"\n        srcset=\"/static/program-notifications-2-30633c0ff5509dcca0809a9eb683d079-67829.png 125w,\n/static/program-notifications-2-30633c0ff5509dcca0809a9eb683d079-5c59f.png 250w,\n/static/program-notifications-2-30633c0ff5509dcca0809a9eb683d079-2f183.png 500w,\n/static/program-notifications-2-30633c0ff5509dcca0809a9eb683d079-1007a.png 714w\"\n        sizes=\"(max-width: 500px) 100vw, 500px\"\n      />\n    </span>\n  </span>\n  \n  </a>\n    \n<br>\n<i>\nThe \n<strong>Subscribe</strong>\n button is currently only enabled for select hackers\n</i>\n.</td>\n</tr>\n<tr>\n<td>Automatically</td>\n<td>You'll automatically receive report notifications if you've:\n<br>\n<ul>\n<li>\nSubmitted a report to a public program\n<br>\n<li>\nAccepted an invite to a private program</td>\n</tr>\n</tbody>\n</table>\n<h3>Manage Notifications</h3>\n<p>You can manage email notifications you receive from programs you belong to in <strong>Settings > Programs</strong>. On the Programs page, you can view a list of programs you belong to along with the visibility of that program on your public profile as well as your notification settings.</p>\n<p>\n  <a\n    class=\"gatsby-resp-image-link\"\n    href=\"/static/manage-notifications-1-e56b45f97324fced1cca299906ac8f2f-7607e.png\"\n    style=\"display: block\"\n    target=\"_blank\"\n    rel=\"noopener\"\n  >\n  \n  <span\n    class=\"gatsby-resp-image-wrapper\"\n    style=\"position: relative; display: block; ; max-width: 500px; margin-left: auto; margin-right: auto;\"\n  >\n    <span\n      class=\"gatsby-resp-image-background-image\"\n      style=\"padding-bottom: 43.9223697650664%; position: relative; bottom: 0; left: 0; background-image: url('data:image/png;base64,'); background-size: cover; display: block;\"\n    >\n      <img\n        class=\"gatsby-resp-image-image\"\n        style=\"width: 100%; height: 100%; margin: 0; vertical-align: middle; position: absolute; top: 0; left: 0; box-shadow: inset 0px 0px 0px 400px white;\"\n        alt=\"manage-notifications-1\"\n        title=\"\"\n        src=\"/static/manage-notifications-1-e56b45f97324fced1cca299906ac8f2f-2f183.png\"\n        srcset=\"/static/manage-notifications-1-e56b45f97324fced1cca299906ac8f2f-67829.png 125w,\n/static/manage-notifications-1-e56b45f97324fced1cca299906ac8f2f-5c59f.png 250w,\n/static/manage-notifications-1-e56b45f97324fced1cca299906ac8f2f-2f183.png 500w,\n/static/manage-notifications-1-e56b45f97324fced1cca299906ac8f2f-c93e8.png 750w,\n/static/manage-notifications-1-e56b45f97324fced1cca299906ac8f2f-7607e.png 979w\"\n        sizes=\"(max-width: 500px) 100vw, 500px\"\n      />\n    </span>\n  </span>\n  \n  </a>\n    </p>\n<p>To change your notification preferences for a program:</p>\n<ol>\n<li>\n<p>Click on the link below <strong>Notifications</strong> for that program. The link will either say <em>Enabled</em> or <em>Disabled</em> depending on your existing notification preference.</p>\n</li>\n<li>\n<p>A window will show where you can set your notifications.\nIf you want to disable notifications, the <strong>Disable Notifications</strong> screen will show where you can unsubscribe from reports.\n\n  <a\n    class=\"gatsby-resp-image-link\"\n    href=\"/static/manage-notifications-3-644ae177806060704bbcfb7056799939-ad87e.png\"\n    style=\"display: block\"\n    target=\"_blank\"\n    rel=\"noopener\"\n  >\n  \n  <span\n    class=\"gatsby-resp-image-wrapper\"\n    style=\"position: relative; display: block; ; max-width: 500px; margin-left: auto; margin-right: auto;\"\n  >\n    <span\n      class=\"gatsby-resp-image-background-image\"\n      style=\"padding-bottom: 51.010101010101%; position: relative; bottom: 0; left: 0; background-image: url('data:image/png;base64,'); background-size: cover; display: block;\"\n    >\n      <img\n        class=\"gatsby-resp-image-image\"\n        style=\"width: 100%; height: 100%; margin: 0; vertical-align: middle; position: absolute; top: 0; left: 0; box-shadow: inset 0px 0px 0px 400px white;\"\n        alt=\"manage-notifications-3\"\n        title=\"\"\n        src=\"/static/manage-notifications-3-644ae177806060704bbcfb7056799939-2f183.png\"\n        srcset=\"/static/manage-notifications-3-644ae177806060704bbcfb7056799939-67829.png 125w,\n/static/manage-notifications-3-644ae177806060704bbcfb7056799939-5c59f.png 250w,\n/static/manage-notifications-3-644ae177806060704bbcfb7056799939-2f183.png 500w,\n/static/manage-notifications-3-644ae177806060704bbcfb7056799939-c93e8.png 750w,\n/static/manage-notifications-3-644ae177806060704bbcfb7056799939-ad87e.png 990w\"\n        sizes=\"(max-width: 500px) 100vw, 500px\"\n      />\n    </span>\n  </span>\n  \n  </a>\n    </p>\n<p>If you want to enable notifications, the <strong>Enable Notifications</strong> screen will show where you can subscribe to reports.\n\n  <a\n    class=\"gatsby-resp-image-link\"\n    href=\"/static/manage-notifications-2-540e06cbf51f48551c04211d79e0b75e-ad87e.png\"\n    style=\"display: block\"\n    target=\"_blank\"\n    rel=\"noopener\"\n  >\n  \n  <span\n    class=\"gatsby-resp-image-wrapper\"\n    style=\"position: relative; display: block; ; max-width: 500px; margin-left: auto; margin-right: auto;\"\n  >\n    <span\n      class=\"gatsby-resp-image-background-image\"\n      style=\"padding-bottom: 50.70707070707071%; position: relative; bottom: 0; left: 0; background-image: url('data:image/png;base64,'); background-size: cover; display: block;\"\n    >\n      <img\n        class=\"gatsby-resp-image-image\"\n        style=\"width: 100%; height: 100%; margin: 0; vertical-align: middle; position: absolute; top: 0; left: 0; box-shadow: inset 0px 0px 0px 400px white;\"\n        alt=\"manage-notifications-2\"\n        title=\"\"\n        src=\"/static/manage-notifications-2-540e06cbf51f48551c04211d79e0b75e-2f183.png\"\n        srcset=\"/static/manage-notifications-2-540e06cbf51f48551c04211d79e0b75e-67829.png 125w,\n/static/manage-notifications-2-540e06cbf51f48551c04211d79e0b75e-5c59f.png 250w,\n/static/manage-notifications-2-540e06cbf51f48551c04211d79e0b75e-2f183.png 500w,\n/static/manage-notifications-2-540e06cbf51f48551c04211d79e0b75e-c93e8.png 750w,\n/static/manage-notifications-2-540e06cbf51f48551c04211d79e0b75e-ad87e.png 990w\"\n        sizes=\"(max-width: 500px) 100vw, 500px\"\n      />\n    </span>\n  </span>\n  \n  </a>\n    </p>\n</li>\n</ol>\n<p>If you disable your notifications, you'll start receiving notifications again when you:</p>\n<ul>\n<li>Interact with a report</li>\n<li>Are assigned a report</li>\n<li>Are mentioned in a report</li>\n</ul>","frontmatter":{"title":"Notifications","id":"hackers/manage-notifications","path":"/hackers/manage-notifications.html","description":null}}},"pathContext":{}}

